  // Case 1: ë‚´í¬ì¥ ì™„ë£Œ í›„ ì™¸í¬ì¥ìœ¼ë¡œ ìë™ ì´ë™
  if (route.query.inner_completed === 'true' || route.query.completed_inner === 'true') {
