function getTotalWorkTime() {
  if (innerCompletionTime.value && outerCompletionTime.value) {
    const diff = outerCompletionTime.value.getTime() - innerCompletionTime.value.getTime()
    const hours = Math.floor(diff / (1000 * 60 * 60))
